*{
  margin: auto;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: rgb(26, 26, 78);
  height: 100%;
  
}
#root{
  width: fit-content;
  height:100%;
  padding: auto;
}

.todo{
  text-align: center;
  width: 100%;
  background-color: rgb(26, 26, 78);
  height: 100%;
}
.input1 {
  width: 50%;
  border: none;
  border-bottom: 1px solid black;
  caret-color: rgba(255, 255, 255, 0.397);
}
.input1:focus {
  border-bottom: 1px solid black;
  outline: none;
}
.btn1 {
  width: 50%;
  padding: 0px 200px;
}
Button {
  width: 100px;
  background-color: rgb(33, 33, 66);
  color: whitesmoke;
  border: none;
  cursor: pointer;
}
.div1 {
  background-color: rgb(37, 37, 116);
    box-shadow: 2px 3px royalblue;
}
h1 {
  background-color: rgb(28, 28, 102);
}
.btn {
    cursor: pointer;
}
.bold{
  left: 14em;
}
.right{
  left: 22rem;
}